<template>
  <el-row style="background: white; padding: 5px 10px; box-sizing: border-box; ">
    <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
      <h1 class="pageTitle">{{ this.prefix }}: {{ this.name }}</h1>
    </el-col>
    <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12" class="text-right">
      <el-button type="primary" @click="editData()">Editar {{ this.prefix }}</el-button>
      <el-button type="primary" @click="deleteData()">Excluir</el-button>
    </el-col>

    <!-- <el-row>
      <el-col :span="24">
        <ul class="subnav">
          <li>Dados do {{ this.prefix }}</li>
          <li>Histórico de Atividade</li>
        </ul>
        <el-divider></el-divider>
      </el-col>
    </el-row>-->

    <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
      <el-row>
        <el-col
          class="data"
          :span="8"
          v-for="(field) in this.dataModuleFields"
          :key="field.field_id"
        >
          <label>{{ field.field_label }}</label>
          <p class="data-value" v-if="field.type_field == 'switch'">
            <span v-if="dataModule[field.field_table_name] == 0">Não</span>
            <span v-if="dataModule[field.field_table_name] == 1">Sim</span>
          </p>

          <p class="data-value" v-if="field.type_field != 'switch'">
            <span>{{ dataModule[field.field_table_name] }}</span>
          </p>
        </el-col>
      </el-row>
    </el-col>

    <UpdateModuleData @done="doneUpdate()" :dataID="this.dataID" :show="this.drawerEditShow" />
  </el-row>
</template>

<script>
import UpdateModuleData from "@/views/restricted/Deals/Update";
export default {
  name: "DealView",
  head: {
    title: {
      inner: "",
      complement: "GuiaSales",
    },
  },
  data() {
    return {
      pageTitle: "",
      dataID: -1,
      dataModule: [],
      dataModuleFields: [],
      prefix: "Oportunidade",
      drawerEditShow: false,
    };
  },
  components: { UpdateModuleData },
  mounted() {
    this.dataID = this.$route.params.id;
    this.getDataObject();
    this.getFields();
  },
  methods: {
    doneUpdate() {
      this.drawerEditShow = false;
      this.getDataObject();
      this.getFields();
    },
    editData() {
      this.drawerEditShow = !this.drawerEditShow;
    },
    getFields() {
      this.$http
        .get("/fields/deals")
        .then((resFields) => {
          this.dataModuleFields = resFields.data;
        })
        .catch((err) => {
          console.log(err.response);
        });
    },
    getDataObject() {
      this.$http.get("/module/deals/" + this.dataID).then((res) => {
        this.dataModule = res.data;
      });
    },
    deleteData() {
      this.$http
        .delete("/module/deals/" + this.dataID)
        .then((res) => {
          res;
          this.$notify.success({
            title: "Sucesso",
          });
          this.$router.push({
            name: "Deals_List",
          });
        })
        .catch((err) => {
          err;
          this.$notify.error({
            title: "Houve um erro a excluir",
          });
        });
    },
  },
  computed: {
    name: function () {
      return this.dataModule.cf_deal_name;
    },
  },
};
</script>

